interface BlendShape {
    Mouth_Drop_Lower?: number;
    Mouth_Shrug_Upper?: number;
    Mouth_Press_R?: number;
    Mouth_Press_L?: number;
    V_Explosive?: number;
    V_Lip_Open?: number;
    V_Dental_Lip?: number;
    V_Tight_O?: number;
    V_Tongue_Out?: number;
    Open_Jaw?: number;
    Tongue?: number;
}

export const OvrToMorph = (
    viseme: { [key: number]: number },
    blendShapeRef: { current: BlendShape[] }
) => {
    if (typeof viseme === "object") {
        const blendShape: BlendShape = {
            Mouth_Drop_Lower: 0,
            Mouth_Shrug_Upper: 0.17,
            Mouth_Press_R: 0,
            Mouth_Press_L: 0,
            V_Explosive: 0,
            V_Lip_Open: 0,
            V_Dental_Lip: 0,
            V_Tight_O: 0,
            V_Tongue_Out: 0,
            Open_Jaw: 0,
            Tongue: 0,
        };

        for (const key in viseme) {
            if (viseme.hasOwnProperty(key)) {
                const visemeValue = viseme[key];
                const currentBlend = Reallusion[VisemeMap[key]];

                for (const blend in currentBlend) {
                    for (const key in viseme) {
                        if (viseme.hasOwnProperty(key)) {
                            const morphKey = key as keyof BlendShape;
                            const morphValue = viseme[key];
                            blendShape[morphKey] = morphValue;
                        }
                    }
                        const blendValue = currentBlend[blend] * visemeValue;
                        blendShape[blend as keyof BlendShape] = (blendShape[blend as keyof BlendShape] || 0) + blendValue;
                    }
                }
            }
        blendShapeRef.current.push(blendShape);
        }
    }